import Sidebar from "@components/sidebar";
import { isObjEmpty } from "@utils";
import classNames from "classnames";
import { useEffect, useState } from "react";
import { useForm, Controller } from "react-hook-form";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import validateOptions from "../../../../../constants/validate";
import { add } from "../store/action";
import Select from "react-select";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { convertToRaw, EditorState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { Editor } from "react-draft-wysiwyg";

const SidebarAdd = ({ open, toggleSidebar, disable, setDisable }) => {
  const product = useSelector((state) => state.products);

  const [discountData, setDiscountData] = useState(null);
  const store = useSelector((state) => state.discounts);
  const lang = useSelector((state) => state.common.language);
  const PriceOptions = validateOptions.PriceOptions;
  const { id } = useParams();
  const dispatch = useDispatch();

  const [valueTechnicalAssistance, setValueTechnicalAssistance] = useState(
    EditorState.createEmpty()
  );

  const cate = yup.object({
    price: yup
      .number()
      .typeError(<FormattedMessage id="The price field is required" />)
      .required(<FormattedMessage id="The price field is required" />)
      .min(0, <FormattedMessage id="Invalid price" />),
    type: yup
      .number()
      .required(<FormattedMessage id="The type field is required" />),
  });

  const {
    register,
    errors,
    handleSubmit,
    control,
    setError,
    setValue,
    clearErrors,
  } = useForm({ resolver: yupResolver(cate), mode: "all" });
  const [changeSelect, setChangeSelect] = useState();

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  const onSubmit = (values) => {
    if (isObjEmpty(errors)) {
      const value_technicala_ssistance =
        (valueTechnicalAssistance &&
          draftToHtml(
            convertToRaw(valueTechnicalAssistance?.getCurrentContent())
          )) ||
        null;
      setDisable(true);
      dispatch(
        add({
          product_id: parseInt(id),
          price: values?.price,
          type: values?.type,
          lang,
          object: values?.object || "",
          system_description: values?.system_description || "",
          platform_description: values?.platform_description || "",
          technical_assistance: value_technicala_ssistance || "",
        })
      );
    }
  };

  const optionType = [
    {
      value: 0,
      label: "Free",
    },
    {
      value: 1,
      label: "Basic",
    },
    {
      value: 2,
      label: "Active personal",
    },
    {
      value: 3,
      label: "Active professional",
    },
  ];

  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="price" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <div className="mt-1"></div>
        <FormGroup>
          <Label for="price">
            <FormattedMessage id="price" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="price"
            id="price"
            type="number"
            innerRef={register(PriceOptions.price)}
            onBlur={() => {
              let discount = document.getElementById("price");
              if (discount && discount.value) {
                discount.value = discount.value.trim();
              }
            }}
            className={classNames({ "is-invalid": errors["price"] })}
          />
          <small className="text-danger">
            {errors?.price && errors.price.message}
          </small>
        </FormGroup>

        {/* {product?.selected?.product_type === 3 && ( */}
        <FormGroup>
          <Label>
            <FormattedMessage id="type" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="type"
            render={({ field }) => {
              return (
                <Select
                  id="type"
                  innerRef={register}
                  name="type"
                  placeholder={<FormattedMessage id="Select type" />}
                  className={classNames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["type"],
                        }
                      : ""
                  )}
                  options={optionType}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    clearErrors("type");
                    setValue("type", e?.value);
                    setChangeSelect(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.type && errors.type.message}
          </small>
        </FormGroup>
        {/* )} */}

        <FormGroup>
          <Label for="object">
            <FormattedMessage id="object" />
          </Label>
          <Input
            name="object"
            id="object"
            innerRef={register(PriceOptions.object)}
          />
        </FormGroup>

        <FormGroup>
          <Label for="system_description">
            <FormattedMessage id="system_description" />
          </Label>
          <Input
            name="system_description"
            id="system_description"
            innerRef={register(PriceOptions.system_description)}
          />
        </FormGroup>

        <FormGroup>
          <Label for="platform_description">
            <FormattedMessage id="platform_description" />
          </Label>
          <Input
            name="platform_description"
            id="platform_description"
            innerRef={register(PriceOptions.platform_description)}
          />
        </FormGroup>

        <FormGroup>
          <Label for="technical_assistance">
            <FormattedMessage id="technical_assistance" />
          </Label>

          <Editor
            stripPastedStyles={true}
            editorState={valueTechnicalAssistance}
            onEditorStateChange={(data) => setValueTechnicalAssistance(data)}
            name="technical_assistance"
            innerRef={register(PriceOptions.technical_assistance)}
          />
        </FormGroup>

        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="Save" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default SidebarAdd;
